<template>
	<div class="add-product-popup">
		<form class="add-product-popup__form" @submit.prevent="handleSubmit">
			<p class="add-product-popup__label">Добавить товар</p>
			<input
				v-model="formData.title"
				class="add-product-popup__title"
				name="title"
				placeholder="Название"
				type="text"
				autofocus
			/>
			<input
				v-model="formData.date"
				class="add-product-popup__date"
				type="date"
				name="date"
				id="date"
			/>
			<select
				v-model="formData.selectedProducts"
				class="add-product-popup__select"
				multiple
				name="products"
				id="products"
			>
				<option
					v-for="product in allOrders"
					:key="product.id"
					:value="product.id"
				>
					{{ product.title }} - {{ product.serialNumber }}
				</option>
			</select>
			<button class="add-product-popup__btn" type="submit">Добавить</button>
		</form>
	</div>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue';
import { useStore } from 'vuex';

const store = useStore();
const allOrders = computed(() => store.getters['Products/allProducts']);

const formData = ref({
	title: '',
	date: '',
	selectedProducts: [],
});

const generateRandomId = () => {
	return 'order_' + Math.random().toString(36).substr(2, 9);
};

const handleSubmit = () => {
	const order = {
		id: generateRandomId(),
		title: formData.value.title,
		date: formData.value.date,
		products: formData.value.selectedProducts,
	};
	store.dispatch('Orders/addOrder', order);
};

onMounted(() => {
	console.log(allOrders.value);
});
</script>

<style lang="scss" scoped src="./add-product-popup.scss" />
