import { defineComponent } from 'vue';
import Product from '@/components/ProductsView/Product/Product.vue';
export default defineComponent({
    components: {
        Product,
    },
    props: {
        products: {
            type: Array,
            required: true,
        },
    },
    setup() {
        const handleBeforeLeave = (el) => {
            el.style.transform = 'translateX(100%)';
            el.style.opacity = '0';
        };
        return {
            handleBeforeLeave,
        };
    },
});
