import { computed } from 'vue';
import { format } from 'date-fns';
import { ru } from 'date-fns/locale';
import { useStore } from 'vuex';
export default {
    props: {
        group: {
            type: Object,
            required: true,
        },
    },
    setup(props) {
        const store = useStore();
        const date = new Date(props.group.date);
        const formattedDate1 = format(date, 'dd / MM');
        const formattedDate2 = format(date, 'dd / MMM / yyyy', { locale: ru });
        const productsCount = computed(() => store.getters['Products/getProductsCountByOrder'](props.group.id));
        return {
            formattedDate1,
            formattedDate2,
            productsCount,
        };
    },
};
