import { computed, ref, watch } from 'vue';
import { useStore } from 'vuex';
export default {
    setup(_, { emit }) {
        const store = useStore();
        const allProducts = computed(() => store.getters['Products/allProducts']);
        const selectedType = ref('');
        const selectedSpecification = ref('');
        const uniqueTypes = computed(() => {
            const types = allProducts.value.map((product) => product.type);
            return [...new Set(types)];
        });
        const uniqueSpecifications = computed(() => {
            const specifications = allProducts.value.map((product) => product.specification);
            return [...new Set(specifications)];
        });
        const filteredProducts = computed(() => {
            return allProducts.value.filter((product) => {
                return ((selectedType.value === '' || product.type === selectedType.value) &&
                    (selectedSpecification.value === '' ||
                        product.specification === selectedSpecification.value));
            });
        });
        const emitFilteredProducts = () => {
            emit('update:filteredProducts', filteredProducts.value);
        };
        watch([selectedType, selectedSpecification, allProducts], emitFilteredProducts, { immediate: true });
        return {
            allProducts,
            selectedType,
            selectedSpecification,
            uniqueTypes,
            uniqueSpecifications,
            filteredProducts,
        };
    },
};
