import { defineComponent } from 'vue';
import { useStore } from 'vuex';
export default defineComponent({
    props: {
        product: {
            type: Object,
            required: true,
        },
    },
    setup(props) {
        const store = useStore();
        const removeProduct = () => {
            store.dispatch('Products/deleteProduct', props.product.id);
        };
        return {
            removeProduct,
        };
    },
});
