import { computed } from 'vue';
import { useStore } from 'vuex';
import Order from '@/components/OrderView/Order/Order.vue';
export default {
    components: {
        Order,
    },
    setup() {
        const store = useStore();
        const allOrders = computed(() => store.getters['Orders/allOrders']);
        return {
            allOrders,
        };
    },
};
