import { defineComponent, computed } from 'vue';
import { useStore } from 'vuex';
import GroupProduct from '@/components/GroupsView/GroupProduct/GroupProduct.vue';
export default defineComponent({
    props: {
        selectedGroup: {
            type: Object,
            required: false,
            default: () => ({}),
        },
    },
    components: {
        GroupProduct,
    },
    setup(props) {
        const store = useStore();
        const products = computed(() => {
            if (!props.selectedGroup || !props.selectedGroup.products) {
                return [];
            }
            return props.selectedGroup.products.map((id) => store.getters['Products/getProductById'](id));
        });
        return {
            products,
        };
    },
});
